@use './scss/' as *;

.about {
    height: 100%;
    h1{
        color: $crown;
        
    }
    background-color: $main-color;
    padding: 40px 0;
   
    .container {

        .box {
            background-color: #b99d7c;
            margin: 3rem 10rem;
            height: 225px;
            box-shadow: $box-shadow;
            &:hover{
                transform:scale(1.01);
                transition: 0.3s ease;
            }
            p {
                font-size: 30px;
                margin: 0 20px;
                font-family: $font-family2;
            }
            img {
                box-shadow: $box-shadow4;
            }
        }

        .box2 {
            img {
                float: right;
                width: 300px;
                transform: translate(30px, -30px);
            }

        }

        .box1 {
            img {
                float: left;
                width: 350px;
                transform: translate(-20px, -20px);
            }
        }

        .box3 {
            img {
                float: left;
                width: 400px;
                transform: translate(-30px, -20px);
            }
        }
    }
}